<template>
    <div id="park" class="park-wrap page">
        <Page :bgSrc="bgSrc" :titleSrc="''">
            <div class="park-content">
                <div class="park-logo">
                    <img src="../assets/park.png" alt="">
                </div>
                <div class="park-desc">
                    AWEAWE<br>
                    <br>
                    <br>
                    <br>
                    <br>
                    <br>
                    <br>
                    VRAR
                </div>
                <Linktag :link="'http://park.jiguo.com/'" :text="''"></Linktag>
                <Down/>
            </div>
        </Page>
    </div>
</template>
<script>
  import Page from './page'
  import Linktag from './link_tag'
  import Down from './down'
  export default {
    data () {
      return {
        bgSrc: require('../assets/park_bg.png')
      }
    },
    components: {
      Page,
      Linktag,
      Down
    }
  }
</script>
<style scoped lang="less">
    .park-wrap{
        position: relative;
        .page-wrap{
            background-size: cover;
        }
    }
    .park-content{
        padding-top: 206px;
        text-align: center;
    }
    .park-logo{
        width: 659px;
        margin: 0 auto;
        img{
            width: 100%;
        }
    }
    .park-desc{
        width: 610px;
        margin: 108px auto 0;
        font-size: 20px;
        line-height: 49px;
        color: #3ADAD1;
    }
    .link-tag{
        margin: 83px 0 144px;
    }
</style>


